import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            title: '我的检查单',
            user: this.$store.state.userInfo.Data,
            haveMore: true,
            page: 1,
            list: [],
            reason: [],
            pageShow: false,
            sorList:[ '日期不限', '检查状态', '交易状态' ],
            dateList: [ 
                {
                    name: '日期不限',
                    val: '-1'
                },
                {
                    name: '最近一周',
                    val: '1'
                },
                {
                    name: '最近一个月',
                    val: '2'
                },
                {
                    name: '最近半年',
                    val: '3'
                },
                {
                    name: '最近一年',
                    val: '4'
                }
            ],
            statusList: [ 
                {
                    name: '全部状态',
                    val: '-1'
                },
                {
                    name: '待检查',
                    val: '1'
                },
                {
                    name: '已检查',
                    val: '2'
                },
                {
                    name: '已关闭',
                    val: '3'
                }
            ],
            buyList: [ 
                {
                    name: '全部状态',
                    val: '-1'
                },
                {
                    name: '未支付',
                    val: '0'
                },
                // {
                //     name: '支付中',
                //     val: '1'
                // },
                {
                    name: '已支付',
                    val: '2'
                },
                // {
                //     name: '支付失败',
                //     val: '3'
                // },
                // {
                //     name: '退款中',
                //     val: '4'
                // },
                {
                    name: '已退款',
                    val: '5'
                },
                // {
                //     name: '退款失败',
                //     val: '6'
                // },
                // {
                //     name: '已取消',
                //     val: '7'
                // },
                {
                    name: '已关闭',
                    val: '8'
                }
            ],
            date: {
                name: '日期不限',
                val: '-1'
            },
            status: {
                name: '全部状态',
                val: '-1'
            },
            buy: {
                name: '全部状态',
                val: '-1'
            },
            idx: null,
            search: ''
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        searchFor(){
            this.page = 1
            this.haveMore = true
            this.pull(false)
        },
        pull(h){
            if(this.haveMore){
                this.$post('InspectionAPI/Censorate/QueryDrCensorateRecordPage',{
                    DrId: this.user.User.DoctorId,
					DateRangeType:this.date.val,
					PayStatus:this.buy.val,
					CheckStatus:this.status.val,
//                     DateRangeType: this.date.val,
//                     PrescriptionStatus: this.status.val,
//                     PrescriptionDealStatus: this.buy.val,
                    PageIndex: this.page,
                    PageSize: 15,
                    //AppVersion: '1.5',
                    KeyName: this.search
                }).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                        this.$nextTick(() => {
                            window.scrollTo(0, 5)
                        })
                    }
                })
            }
        },
        sort(idx){
            if(this.idx == idx){
                this.idx = null
            }else{
                this.idx = idx
            }
        },
        chose(i){
            this.date = i
            this.sorList[this.idx] = i.name
            this.idx = null
            this.haveMore = true
            this.page = 1
            this.pull(false)
        },
        chose1(i){
            this.status = i
            this.sorList[this.idx] = i.name
            this.idx = null
            this.haveMore = true
            this.page = 1
            this.pull(false)
        },
        chose2(i){
            this.buy = i
            this.sorList[this.idx] = i.name
            this.idx = null
            this.haveMore = true
            this.page = 1
            this.pull(false)
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        }
    }
}



